import {customElement, stringToHtml} from '@here/hds-base';
import {Icon} from '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import {ListItem} from '@here/hds-list';
import {html, nothing, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-rich-list-item.css.js';
import * as HDSProgressIndicator from '@here/hds-progress-indicator';

export type RichListItemState = 'default' | 'progress' | 'error' | 'success';
export type RichListItemVariant = 'default' | 'dense';
export type RichListItemAction = 'retry' | 'delete' | 'cancel';
export type RichListItemActionPayload = {id: string; action: string; file?: File};

/**
 *
 * @slot action Content for custom actions on the right side
 *
 * @event {CustomEvent<{id: string, action: string, file?: File}>} action Fired when one of the action buttons has been clicked `RichListItemActionPayload`
 */
@customElement('hds-rich-list-item')
export class RichListItem extends ListItem {
  @property({type: String, attribute: false}) richVariant: RichListItemVariant = 'default';
  @property({type: String, attribute: 'state'}) state: RichListItemState = 'default';
  @property({type: String}) headline = '';
  @property({type: String, attribute: 'sub-title'}) subTitle = '';
  @property({type: String, attribute: 'progress-text'}) progressText = '';
  @property({type: Number, attribute: 'progress'}) progress = 0;
  @property({type: String, attribute: 'progress-variant'})
  progressVariant: HDSProgressIndicator.ProgressIndicatorVariant = 'determinate';
  @property({type: String, attribute: 'error-text'}) errorText = '';
  @property({type: Object, attribute: false}) file: File | undefined;
  @property({type: Boolean, attribute: 'file-preview'}) filePreview = false;
  @property({type: String, attribute: 'icon-default'}) iconDefault = 'time';
  @property({type: String, attribute: 'icon-default-category'}) iconDefaultCategory: IconCategory =
    'core-ui';
  @property({type: String, attribute: 'icon-progress'}) iconProgress = 'time';
  @property({type: String, attribute: 'icon-progress-category'})
  iconProgressCategory: IconCategory = 'core-ui';
  @property({type: String, attribute: 'icon-error'}) iconError = 'alert';
  @property({type: String, attribute: 'icon-error-category'}) iconErrorCategory: IconCategory =
    'core-ui';
  @property({type: String, attribute: 'icon-success'}) iconSuccess = 'checkmark';
  @property({type: String, attribute: 'icon-success-category'}) iconSuccessCategory: IconCategory =
    'core-ui';
  @property({type: Boolean, attribute: 'hide-icon'}) hideIcon = false;
  @property({type: Boolean, attribute: 'hide-cancel'}) hideCancel = false;
  @property({type: Boolean, attribute: 'hide-retry'}) hideRetry = false;
  @property({type: Boolean, attribute: 'hide-delete'}) hideDelete = false;
  @property({type: Boolean, attribute: false}) unhoverable = false;
  @property({type: Boolean, attribute: false}) unpressable = false;
  @property({type: Boolean, attribute: false}) unselectable = false;
  @property({type: Boolean, attribute: false}) unfocusable = false;
  @query('img') private imgFilePreview!: HTMLImageElement;

  static get styles(): any {
    return [super.styles, styles];
  }

  protected async updated(_changedProperties: PropertyValues) {
    await this.updateComplete;

    super.updated(_changedProperties);

    if (_changedProperties.has('file')) {
      this._loadFilePreview();
    }
  }

  protected render() {
    const title =
      this.file !== undefined
        ? this.file.name.replace(/(\.[a-zA-Z0-9]+)$/, '<span>$1</span>')
        : this.headline;

    const canRenderErrorText = this.state === 'error' && this.errorText !== '';
    const subTitleText = canRenderErrorText ? this.errorText : this.subTitle;

    const classes = {
      item: true,
      '-hds-rich-list-item-wrapper': true,
      '-hds-list-item-wrapper': true,
      'hds-list-item-variant-solid-default': true,
      'hds-list-item-h-padding-dense': true,
      disabled: this.disabled,
      dragging: this.dragging,
      selected: this.selected,
      [`${this.unhoverable ? 'unhoverable' : 'hoverable'}`]: true,
      [`${this.unselectable ? 'unselectable' : 'selectable'}`]: true,
      [`${this.unpressable ? 'unpressable' : 'pressable'}`]: true,
      [`${this.unfocusable ? 'unfocusable' : 'focusable'}`]: true,
      'has-error-text': canRenderErrorText,
      'has-sub-title-text': subTitleText,
    };

    classes[`-rich-list-item-variant-${this.richVariant}`] = true;
    if (this.richVariant === 'default') {
      classes['hds-list-item-size-extra-large'] = true;
    } else if (this.richVariant === 'dense') {
      classes['hds-list-item-size-small'] = true;
    }
    classes[`-rich-list-item-state-${this.state}`] = true;
    if (this.state === 'error') {
      classes['hds-list-item-variant-solid-error'] = true;
    }

    return html`
      <div class="${classMap(classes)}" @click="${super.onItemClick}" tabindex="-1">
        <div class="thumbnail">${this._renderFilePreview()} ${this._renderIcon()}</div>
        <div class="content-wrapper">
          <div class="upper">
            <div class="title">${stringToHtml(title)}</div>
            <div class="progress">${this.progressText}</div>
          </div>
          <div class="lower">
            <div class="sub-title">${subTitleText}</div>
          </div>
          ${this._renderProgressBar()}
        </div>
        <div class="actions">
          <slot name="action" @click="${this._emitEventOnAction}"></slot>
          ${this._renderAction('retry', 'refresh')} ${this._renderAction('cancel', 'cross')}
          ${this._renderAction('delete', 'delete')}
        </div>
      </div>
    `;
  }

  private canRenderFilePreview() {
    return this.filePreview && this.file !== undefined && this.file.type.indexOf('image/') === 0;
  }

  private async _emitEventOnAction(e) {
    e.stopPropagation();

    const action = e.target.getAttribute('data-action') || 'custom';
    const detail: any = {id: this.id, action};

    if (this.file !== undefined) {
      detail.file = this.file;
    }
    await this.updateComplete;
    this.dispatchEvent(
      new CustomEvent<RichListItemActionPayload>('action', {
        detail,
        bubbles: true,
      })
    );
  }

  private _renderIcon() {
    if (this.hideIcon || this.canRenderFilePreview()) {
      return nothing;
    }

    const iconProps = {
      'default--default': {
        size: '24px',
        style: 'outline',
        name: this.iconDefault,
        category: this.iconDefaultCategory,
      },
      'default--progress': {
        size: '24px',
        style: 'outline',
        name: this.iconProgress,
        category: this.iconProgressCategory,
      },
      'default--error': {
        size: '24px',
        style: 'outline',
        name: this.iconError,
        category: this.iconErrorCategory,
      },
      'default--success': {
        size: '24px',
        style: 'outline',
        name: this.iconSuccess,
        category: this.iconSuccessCategory,
      },
      'dense--default': {
        size: '16px',
        style: 'solid',
        name: this.iconDefault,
        category: this.iconDefaultCategory,
      },
      'dense--progress': {
        size: '16px',
        style: 'solid',
        name: this.iconProgress,
        category: this.iconProgressCategory,
      },
      'dense--error': {
        size: '16px',
        style: 'solid',
        name: this.iconError,
        category: this.iconErrorCategory,
      },
      'dense--success': {
        size: '16px',
        style: 'solid',
        name: this.iconSuccess,
        category: this.iconSuccessCategory,
      },
    }[`${this.richVariant}--${this.state}`];

    if (this.file !== undefined) {
      iconProps.name = Icon.getNameByMediaType(this.file.type);
      iconProps.category = 'file';
    }

    return html`
      <hds-icon
        name="${iconProps.name}"
        size="${iconProps.size}"
        category="${iconProps.category}"
        icon-style="${iconProps.style}"
      >
      </hds-icon>
    `;
  }

  private _renderProgressBar() {
    if (this.state === 'progress') {
      return html`
        <div class="progress-wrapper">
          <hds-progress-indicator
            color="${this.selected ? 'on-dark' : 'action'}"
            type="linear"
            variant="${this.progressVariant}"
            ?disabled="${this.disabled}"
            progress="${this.progress}"
          ></hds-progress-indicator>
        </div>
      `;
    }

    return nothing;
  }

  private _renderFilePreview() {
    if (this.canRenderFilePreview()) {
      return html`<img width="40" height="40" alt="preview" />`;
    }
    return nothing;
  }

  private _loadFilePreview() {
    if (this.canRenderFilePreview()) {
      this.imgFilePreview.src = '';
      const reader = new FileReader();

      reader.onloadend = () => {
        this.imgFilePreview.src = reader.result as string;
      };

      reader.readAsDataURL(this.file as File);
    }
  }

  private _renderAction(action: RichListItemAction, icon: string) {
    const canRenderRetry = action === 'retry' && this.state === 'error' && !this.hideRetry;

    const canRenderCancel =
      action === 'cancel' &&
      ['default', 'progress', 'error'].includes(this.state) &&
      !this.hideCancel;

    const canRenderDelete = action === 'delete' && this.state === 'success' && !this.hideDelete;

    if (canRenderRetry || canRenderCancel || canRenderDelete) {
      return html` <hds-icon
        @click="${this._emitEventOnAction}"
        data-action="${action}"
        name="${icon}"
        size="16px"
      >
      </hds-icon>`;
    }
    return nothing;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-rich-list-item': RichListItem;
  }
}
